<template>
  <div id="signup">
    <div class="dangnhap" v-if="form">
      <h3>Dang Nhap</h3>
      <hr />
      <form @submit.prevent="dangnhap">
        <div class="mb-3">
          <label class="form-label">Email address</label>
          <input type="email" class="form-control" v-model="userL.email" />
        </div>
        <div class="mb-3">
          <label class="form-label">Password</label>
          <input
            type="password"
            class="form-control"
            v-model="userL.passworld"
          />
        </div>
        <div class="alert alert-danger" role="alert" v-if="status">
          {{ status }}
        </div>
        <div class="alert alert-warning" role="alert" v-if="xacthuc">
          Tai khoan chua duoc xac thuc! <br />
          Tai khoan khong xac thuc se khong the dang bai viet va binh luan!
        </div>
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" v-if="xacthuc" @click="reset">Close</button>
        <button type="submit" class="btn btn-primary" v-if="!xacthuc">Dang nhap</button> Neu
        chua co tai khoan bam <a href="" @click.prevent="chuyendoi">dang ky</a>
      </form>
    </div>
    <div class="dangky" v-else>
      <h3>Dang Ki</h3>
      <hr />
      <form @submit.prevent="dangky">
        <div class="mb-3">
          <label class="form-label">Email address</label>
          <input type="email" class="form-control" v-model="userR.email" />
        </div>
        <!-- <div class="mb-3">
          <label class="form-label">Nickname</label>
          <input type="email" class="form-control" v-model="userR.nickname" />
        </div> -->
        <div class="mb-3">
          <label class="form-label">Password</label>
          <input
            type="password"
            class="form-control"
            v-model="userR.passworld"
          />
        </div>
        <div class="mb-3">
          <label class="form-label">Re-Password</label>
          <input
            type="password"
            class="form-control"
            v-model="userR.repassworld"
          />
        </div>
        <div class="alert alert-danger" role="alert" v-if="status">
          {{ status }}
        </div>
        <div class="alert alert-warning" role="alert" v-if="xacthuc">
          Da dang ki thanh cong. Vui long vao mail cua ban de xac thuc tai
          khoan! <br />
          Tai khoan khong xac thuc se khong the dang bai viet va binh luan!
        </div>
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" v-if="xacthuc" @click="reset">Close</button>
        <button type="submit" class="btn btn-primary" v-if="!xacthuc">Dang Ky</button> Neu da co
        tai khoan bam <a href="" @click.prevent="chuyendoi">dang nhap</a>
      </form>
    </div>
  </div>
</template>

<!--------->

<script>
import {
  projectAuth,
  createUserWithEmailAndPassword,
} from "../configs/firebase";
import {
  signInWithEmailAndPassword,
  sendEmailVerification,
} from "firebase/auth";

export default {
  data: () => {
    return {
      userL: {
        email: "",
        passworld: "",
      },
      userR: {
        email: "",
        passworld: "",
        repassworld: "",
      },
      status: null,
      form: true,
      xacthuc: false,
    };
  },
  methods: {
    dangky: function () {
      this.status = null
      if (this.userR.passworld == this.userR.repassworld) {
        createUserWithEmailAndPassword(
          projectAuth,
          this.userR.email,
          this.userR.passworld
        )
          .then((userCredential) => {
            // Signed in
            const userA = userCredential.user;
            this.xacthuc = true;
            sendEmailVerification(projectAuth.currentUser).then(() => {
              // Email verification sent!
              console.log("Da gui xac minh");
            });
          })
          .catch((err) => {
            if (err.code == "auth/weak-password") {
              this.status = "Mat khau qua ngan!";
            } else if (err.code == "auth/email-already-in-use") {
              this.status = "Email da ton tai, vui long chon email khac!";
            } else {
              this.status = err.code;
            }
          });
      } else {
        this.status = "Mat khau ko trung khop!";
      }
    },
    dangnhap() {
      this.status = null
      signInWithEmailAndPassword(
        projectAuth,
        this.userL.email,
        this.userL.passworld
      )
        .then((userCredential) => {
          const user = userCredential.user;
          if(!user.emailVerified) {
            this.xacthuc = true
          }
        })
        .catch((err) => {
          if (err.code == "auth/wrong-password") {
            this.status = "Sai mat khau!";
          } else if ((err.code = "auth/user-not-found")) {
            this.status = "Tai khoan khong ton tai!";
          } else {
            this.status = err.code;
          }
        });
    },
    chuyendoi() {
      this.form = !this.form;
      this.status = null;
      this.xacthuc = false
    },
    reset() {
      this.xacthuc = false
    }
  },
};
</script>