<template>
  <div class="post-list">
    <ul class="list-group">
      <li class="list-group-item row" v-for="post in posts" :key="post.id" style="display:flex;padding-left:0;padding-right:0">
        <div class="col-4">
          <img :src="post.img" alt="" style="width:100%">
        </div>
        <div class="col">
          <router-link :to="post.id"><h4>{{ post.title }}</h4></router-link>
          {{post.time}}
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { collection, getDocs, getFirestore } from "firebase/firestore";
import store from '../store/index'

export default {
  data:() =>{
    return {
      posts: []
    }
  },
  created() {
    (async () => {
      const querySnapshot = await getDocs(collection(getFirestore(), "posts"));
      var bien = 0
      querySnapshot.forEach((doc) => {
        bien++
        const post = {
          title: '',
          id: '',
          img: '',
          time: ''
        }
        if(doc.data().title.length > 70) post.title = doc.data().title.substring(0,70) + '...'
        else post.title = doc.data().title
        post.id = '/post/' + doc.id
        post.img = doc.data().img
        post.time = doc.data().time
        this.posts.push(post)
        store.commit("TOTAL_POST", bien);
      });
    })();
  },
};
</script>

