import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

import store from '../store/index'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/post',
        name: 'Post',
        component: () =>
            import ( /* webpackChunkName: "post" */ '../views/Post.vue')
    },
    {
        path: '/post/:slug',
        name: 'PostView',
        component: () =>
            import ( /* webpackChunkName: "view" */ '../views/Post.vue')
    },
    {
        path: '/addpost',
        name: 'AddPost',
        component: () =>
            import ( /* webpackChunkName: "addpost" */ '../components/Addpost.vue')
    },
    {
        path: '/addpost/:slug',
        name: 'EditPost',
        component: () =>
            import ( /* webpackChunkName: "editpost" */ '../components/Addpost.vue')
    },
    {
        path: '/admin',
        name: 'Admin',
        component: () =>
            import ( /* webpackChunkName: "admin" */ '../views/Admin.vue')
    },
    {
        path: '/profile',
        name: 'Profile',
        component: () =>
            import ( /* webpackChunkName: "profile" */ '../views/Profile.vue')
    },
    {
        path: '/reg',
        name: 'Reg',
        component: () =>
            import ( /* webpackChunkName: "reg" */ '../composables/SignUp.vue'),
    },
    {
        path: '/*',
        name: '404',
        component: () =>
            import ( /* webpackChunkName: "admin" */ '../views/404.vue')
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.beforeEach((to, from, next) => {
    if (to.name == 'Profile' && !store.state.isAuth) next({ name: 'Home' })
    else if (to.name == 'AddPost' && !store.state.isAuth) next({ name: 'Home' })
    else if (to.name == 'EditPost' && !store.state.isAuth) next({ name: 'Home' })
    else next()
})

export default router