import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        isAuth: false,
        user: {
            name: ''
        },
        post: {
            totalPost: 0
        }
    },
    getters: {

    },
    mutations: {
        IS_AUTH(state) {
            state.isAuth = !state.isAuth
        },
        SET_NAME(state, name) {
            state.user.name = name
        },
        TOTAL_POST(state, total) {
            state.post.totalPost = total
        }
    },
    actions: {

    }
})

export default store