<template>
  <div class="container">
    <div class="row">
      <div class="col-2"></div>
      <div class="col-7 fs-1" style="padding-left:0">
        <nav aria-label="breadcrumb">
          <ol class="breadcrumb text-secondary">
            <li class="breadcrumb-item" @click="home"><router-link to="/">Trang Chủ</router-link></li>
            <!-- <li class="breadcrumb-item" @click="addpost" v-if="isAuth"><router-link to="/addpost">Viết Bài</router-link></li> -->
          </ol>
        </nav>
        <nav aria-label="breadcrumb" style="margin-top:-15px">
          <ol class="breadcrumb text-secondary mb-2">
            <li class="breadcrumb-item active" aria-current="page">{{ pos }}</li>
          </ol>
        </nav>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: "Breadcrumb",
  data: () => {
    return {
      pos: 'post list'
    }
  },
  computed: mapState(['isAuth']),
  methods:{
    home: function(){
      this.pos = 'post list';
    },
    addpost: function(){
      this.pos = 'Add Post';
    }
  }
};
</script>

<style scoped>
.breadcrumb {
  opacity: 0.3;
  font-family: Bungee;
  margin: 15px 0;
}
.breadcrumb a{
  text-decoration: none;
}
.breadcrumb a:hover{
  opacity: 1;
}
</style>
